import React from 'react';
import { Link } from 'react-router-dom'
import { NavLink } from 'react-router-dom';
import { useLocation } from "react-router-dom";
import ModalSmall from './ModalSmall';
import logo from '../images/logo.png';
import close from '../images/close-icon.png'
import { 
    Navbar, 
    NavItem, 
    NavbarToggler, 
    Collapse, 
    Nav, 

} from 'reactstrap'; 








const NavbarMenu = () => {
    const [open, setOpen] = React.useState(false);
 
    const handleClose = () => {
        setOpen(false);
    };
 
    const handleOpen = () => {
        setOpen(true);
    };
    const location = useLocation();

    //destructuring pathname from location
    const { pathname } = location;

    //Javascript split method to get the name of the path in array
    const splitLocation = pathname.split("/");


    // Collapse isOpen State 
    const [isOpen, setIsOpen] = React.useState(false); 

    return(
   <>
    <div className="row borNav">
        <div className="col-md-12">
            <Navbar className="navbar navbar-expand-lg navbar-light"> <Link className="navbar-brand" to="../inicio"> <img src={logo} width={150} alt="Tukysa" className="p-2"/> </Link>
            <NavbarToggler onClick={() => { setIsOpen(!isOpen) }} /> 
                <Collapse isOpen={isOpen} navbar> 
                <Nav className="mr-auto navbar-nav fs15 d-flex justify-content-around w-100" navbar> 
                
                <NavItem  className="nav-item pl-2 pr-2 mt-2 pt-2"><NavLink className={`navLink ${splitLocation[1] === "papers"  ? "active" : "navLink"}`} activeClassName="active" to="/papers">Papers</NavLink></NavItem> 
                    <li className="nav-item dropdown mt-2 pt-2">
                    <Link className="navLink" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Manejo</Link>
                        <div className="dropdown-menu p-4" aria-labelledby="navbarDropdown">
                        <NavLink className="dropdown-item blue p-2" to="/posologia" >Posología y dosificación</NavLink>
                        <NavLink className="dropdown-item blue p-2" to="/reacciones">Manejo de reacciones adversas</NavLink>
                        <NavLink className="dropdown-item blue p-2" to="/interacciones" >Interacciones con otros medicamentos</NavLink>
                        </div>
                    </li>
                    <NavItem className="nav-item pl-2 pr-2 pt-2 mt-2"> <NavLink className={`navLink ${splitLocation[1] === "cursos"  ? "active" : "navLink"}`} to="/cursos" activeClassName="active">Cursos acreditados</NavLink> </NavItem> 
                    <NavItem className="nav-item pl-2 pr-2 mt-3"> <Link className="menuButton" onClick={handleOpen}>Servicio de información científica</Link> </NavItem> 
                    </Nav> 
                </Collapse> 
                </Navbar> 
          
        </div>
    </div>
    <div
            style={{
                textAlign: "center",
                display: "block",
                padding: 2,
                margin: "auto",
            }}
        >
            <ModalSmall isOpen={open} onClose={handleClose}>
                <>
                <p class="text-right"><Link onClose={handleClose}><img src={close} width={43} alt="cerrar"/></Link></p>
                   
                <p class="fs18 grey text-center pt-5"><b>Solicite información adicional sobre nuestros medicamentos o programas de investigación a nuestro servicio de MedInfo a través del siguiente email:</b></p>
                    <p class="fs20 blue text-center pt-4"><a class="fs20 blue text-center mailLink" href="mailto:medinfoeu@seagen.com"><b>medinfoeu@seagen.com</b></a></p>
               
                </>
            </ModalSmall>
        </div>

  </>
    )
}
export default NavbarMenu