                <p className="grey fs20 text-justify pl-4 ml-2">Un estudio clínico de interacciones farmacológicas demostró que la administración conjunta de una dosis única de 300 mg de <b className='orange'>tucatinib</b> con rifampicina (un inductor potente del CYP3A y moderado del CYP2C8) conducía a una reducción de las concentraciones de <b className='orange'>tucatinib</b> (0,6 veces la C<sub>máx</sub> (IC 90 %: 0,5; 0,8) y 0,5 veces el AUC (IC 90 %: 0,4; 0,6)). Debe evitarse la administración conjunta de <b className='orange'>tucatinib</b> con inductores potentes del CYP3A o moderados del CYP2C8 como la rifampicina, la fenitoína, la hierba de San Juan o la carbamazepina, ya que esto puede dar lugar a una disminución de la actividad de <b className='orange'>tucatinib</b> (ver sección 4.4 de la ficha técnica).</p>
