                <p className="grey fs20 text-justify pl-4 ml-2">Un estudio clínico de interacciones farmacológicas demostró que la administración conjunta de una dosis única de 300 mg de <b className='orange'>tucatinib</b> con gemfibrozilo (un inhibidor potente del CYP2C8) conducía a un aumento de las concentraciones de <b className='orange'>tucatinib</b> (1,6 veces la C<sub>máx</sub> (IC 90 %: 1,5; 1,8) y 3,0 veces el AUC (IC 90 %: 2,7; 3,5)). Debe evitarse la administración conjunta de <b className='orange'>tucatinib</b> con inhibidores potentes del CYP2C8, como gemfibrozilo, ya que esto puede aumentar el riesgo de toxicidad de <b className='orange'>tucatinib</b> (ver sección 4.4 de la ficha técnica).</p>
