                <p className="grey fs20 text-justify pl-4 ml-2"><b className='orange'>Tucatinib</b> es un inhibidor potente del CYP3A. Un estudio clínico de interacciones farmacológicas demostró que la administración conjunta de <b className='orange'>tucatinib</b> con midazolam (un sustrato sensible del CYP3A) conducía a un aumento de las concentraciones de midazolam (3,0 veces la C<sub>máx</sub> (IC 90 %: 2,6; 3,4) y 5,7 veces el AUC (IC 90 %: 5,0; 6,5)). La administración conjunta de <b className='orange'>tucatinib</b> con sustratos sensibles del CYP3A como alfentanilo, avanafilo, buspirona, darifenacina, darunavir, ebastina, everólimus, ibrutinib, lomitapida, lovastatina, midazolam, naloxegol, saquinavir, simvastatina, sirólimus, tacrólimus, tipranavir, triazolam y vardenafilo puede aumentar sus exposiciones sistémicas, lo que puede aumentar la toxicidad asociada a un sustrato de CYP3A. Debe evitarse el uso concomitante de tucatinib con sustratos del CYP3A, cuando los cambios mínimos de concentración pueden dar lugar a efectos adversos graves o que supongan un riesgo vital. Si el uso concomitante es inevitable, la posología del sustrato del CYP3A se debe reducir de acuerdo con la ficha técnica del medicamento concomitante.</p>
