import React from "react";
 
const ModalSmall = ({ isOpen, onClose, children }) => {
    if (!isOpen) return null;
 
    return (
        <div
            onClick={onClose}
            style={{
                position: "fixed",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%",
                background: "rgba(0, 0, 0, 0.5)",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                zIndex:99,
            }}
        >
            <div
                style={{
                    background: "white",
                    height: 350,
                    width: "75%",
                    margin: "auto",
                    padding: "2%",
                    border: "2px solid #7fcacd",
                    borderRadius: "10px",
                    boxShadow: "2px solid black",
                }}
            >
                {children}
            </div>
        </div>
    );
};
 
export default ModalSmall;